import moment from 'moment';

export function getNextEvent(arr, momentNow) {
  return arr.reduce((acc, val) => {
    const momentDateEnd = moment.parseZone(val.dateEnd);
    if (acc.dateEnd) {
      return acc;
    }
    if (momentNow.isBefore(momentDateEnd)) {
      return val;
    }
    return acc;
  }, {});
}



