import React, { Component } from 'react';
import moment from 'moment';
import Widget from './Widget';
import WidgetBase from './WidgetBase';
import WidgetHeader from './WidgetHeader';
import WidgetBody from './WidgetBody';
import WidgetFooter from './WidgetFooter';

class WidgetGarbage extends WidgetBase {
  render() {
    // set state of "in 4 days" in the constructor, then check if string will change
    // this will prevent rerender
    const nextEvent = this.props.data.reduce((acc, val) => {
      if (acc.start) {
        return acc;
      }
      const momentDate = moment.parseZone(val.start);
      if (moment().isBefore(momentDate)) {
        return val;
      }
      return acc;
    }, {});
    const date = nextEvent.start ? moment.parseZone(nextEvent.start).format('lll') : '';
    // const nextDate = moment.parseZone(nextPickup.date).add(7, 'hours');
    // const value1 = nextPickup.date ? nextDate.format('MMM D, YYYY') : '';
    // const value2 = nextPickup.date ?  momentNow.to(nextDate) : '';
    // const shouldWarn = nextPickup.date && nextDate.diff(momentNow, 'hours', true) < 24;
    // const icon = './icons/other/svg/013-recycling-bin.svg';
    const rows = this.props.data.map((x) => {
      return <tr key={x.uid}><td className="text-right no-wrap">{moment.parseZone(x.start).format('MMM D h:mm a')}</td><td>{x.summary}</td></tr>;
    });
    return (
      <Widget expanded={this.state.expanded}>
        <WidgetHeader title="Sasktel Centre" value1={date} value2={nextEvent.summary} icon='' toggleExpanded={this.toggleExpanded} />
        <div className="row-body">
          <table className="table table--collapse table--striped">
            <thead>
            <tr>
              <th>Date</th>
              <th>Event</th>
            </tr>
            </thead>
            <tbody>
            {rows}
            </tbody>
          </table>

        </div>
        <WidgetFooter />
      </Widget>
    );
  }
}

export default WidgetGarbage;



